@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;600&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oswald",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}

/* Keeps the logo in place prevents it from stretching as App is developed */
.app_header {
  object-fit: contain;
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid rgb(151, 7, 132);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.app {
  background-color: white;
}

.app_signUp {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

.app__headerImage {
  object-fit: contain;
}

.app__posts {
  padding: 20px;
}

.app__postsRight {
  margin-left: 20px;
}

.post {
  max-width: 500px;
  background-color: white;
  border: 1px solid rgb(151, 7, 132);
  /* Margin-bottom Puts gaps between the posted images */
  margin-bottom: 45px;
}

.post_image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid rgb(151, 7, 132);
  border-bottom: 1px solid rgb(151, 7, 132);
}

/* font-weight:normal changes the h4 from bold to normal text */
.post_text {
  font-weight: normal;
  padding: 20px;
}

/* display: flex puts two elements in a container into a single row */
.post_header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
}

.post_avatar {
  margin-right: 10px;
}

.post__commentBox {
  display: -webkit-flex;
  display: flex;
  margin-top: 10px;
}

/* When you have two children classNames under a parent className, you can set size priority by assigning 1 or 0 */
.post__input {
  -webkit-flex: 1 1;
          flex: 1 1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
}

.post__button {
  -webkit-flex: 0 1;
          flex: 0 1;
  color: lightgreen;
  background-color: transparent;
  border: none;
  border-top: 1px solid lightgray;
}

.imageUpload {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 10px;
}

.imageUpload__progress {
  width: 100%;
}

